.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  background-color: #F0F0F0;
  color: #fff;
  font-family: 'Roboto Condensed', sans-serif;
}

.FrontPageContainer {
  background: -webkit-radial-gradient(at right 50%, #215471, #13385B);
  background: -o-radial-gradient(at right 50%, #215471, #13385B);
  background: radial-gradient(at right 50%, #215471, #13385B);
  width: 100%;
  height: 100%;
}

.FrontPage {
  margin: 0px auto;
  padding-top: 200px;
  width: 900px;
  text-align: center;
}

h1 {
  font-family: 'Maven Pro', sans-serif;
  font-weight: normal;
}

.FrontPage h1 {
  font-size: 96px;
  margin-bottom: 0px;
}

.in {
  color: #72C9D2;
}

.FrontPage h2 {
  font-weight: normal;
  font-size: 36px;
  color: #62C3CD;
}

button {
  font-family: 'Maven Pro', sans-serif;
  color: #1E3F5F;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 20px;
  padding: 10px 20px;
  background-color: #62C3CD;
  border: 0px;
  border-radius: 5px;
}

input[type="text"] {
  border: 0px;
  padding: 10px;
  font-size: 20px;
  border-radius: 5px;
  width: 367px;
  margin-right: 10px;
  font-family: 'Roboto Condensed', sans-serif;
}

.SearchPage h1 {
  display: inline-block;
  margin: 10px 40px;
}

.SearchPage .header {
  background: -webkit-radial-gradient(at right 100%, #215471, #13385B);
  background: -o-radial-gradient(at right 100%, #215471, #13385B);
  background: radial-gradient(at right 100%, #215471, #13385B);
  height: 70px;
}

.SearchPage .container {
  width: 1500px;
  margin: 0px auto;
  color: #13385B;
  font-size: 24px;
}

.results {
  width: 780px;
}

.results .videoInfo {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  margin-bottom: 20px;
}

.videoMeta {
  padding-left: 20px;
}

.videoInfo img {
  height: 162px;
}

.videoMeta h3 {
  font-size: 22px;
  font-weight: normal;
  margin: 0px;
}

.videoMeta p {
  font-size: 14px;
  color: #767678;
  margin-top: 5px;
}

.videoMeta .byline {
  color: #13385B;
  margin: 0px;
}

.videoMeta .transcript {
  font-size: 14px;
  background-color: #F4F4F4;
  padding: 5px;
}

.transcript .line {
  display: -ms-flexbox;
  display: flex;
  padding: 2px;
  cursor: pointer;
}

.transcript .line:hover {
  background-color: #eee;
}

.transcript .time {
  width: 50px;
  color: #6A6A6A;
}

.SearchPage .container {
  display: -ms-flexbox;
  display: flex;
}

.SearchPage .results {
  margin-right: 30px;
}

.videoArea {
  position: fixed;
  margin-left: 800px;
}

.videoArea .transcript {
  font-size: 14px;
  background-color: #fff;
  padding: 5px;
}

.videoArea p {
  margin-bottom: 10px;
}

.videoArea .transcript, .videoInfo {
  border-radius: 5px;
  border: 1px solid #ccc;
}

.reactRoot, #root {
  width: 100%;
  height: 100%;
}

.header form {
  display: inline;
}

.transcript.full {
  overflow-y: scroll;
  height: 300px;
}

.videoMeta a {
  cursor: pointer;
}

.videoMeta a:hover {
  text-decoration: underline;
}

span.highlight {
  background-color: #ffff00;
}


